<template>
    <div class="root">

        <title-bar :title="'我'"></title-bar>

        <div class="content" v-if="user != null">

            <div class="info-container">

                <img class="icon"
                     :src="user.headimg"
                     src="../../assets/icon.jpg"/>

                <div class="info-content">
                    <span class="info-title">{{user.username}}</span>
                    <span class="info-sub">手机号: {{user.mobile}}</span>
                </div>

            </div>


            <div class="lucky-item">
                <span>您今日幸运中奖号码为:</span>
                <div class="ball-container">

                    <div v-for="ball in redBalls" class="ball">{{ball}}</div>
                    <div class="ball blue">{{blueBall}}</div>

                </div>
                <div class="btn-buy-container">
                    <div class="btn-buy">购买立即中大奖</div>
                </div>
            </div>

            <div class="list-item">
                <span>累计中奖次数:xx</span>
                <span>累计中奖金额:xx</span>
            </div>

            <div class="list-item">
                <span>账户余额:{{user.money}}员</span>
            </div>

            <div class="list-item">
                <span>我的订单</span>
            </div>

            <div class="list-item">
                <span>基本信息</span>
            </div>

            <div class="list-item">
                <span>设置</span>
            </div>

            <div class="btn-logout" @click="clickLogout">
                <span>退出</span>
            </div>

        </div>

    </div>
</template>

<script>
    import TitleBar from "../../components/TitleBar";
    import BallUtils from "../../utils/BallUtils"

    import {mapActions, mapGetters} from 'vuex'

    export default {
        name: "MeTab",
        components: {TitleBar},
        computed: {
            ...mapGetters({
                user: 'user'
            })
        },
        data() {
            return {
                redBalls: [],
                blueBall: "01"
            }
        },
        mounted() {
            this.redBalls = BallUtils.rdmRedBalls();
            this.blueBall = BallUtils.rdmBlueBall();

            this.selectTab(2)
            this.enableTab(true)
        },
        methods: {
            ...mapActions({
                selectTab: 'selectTab',
                enableTab: 'enableTab',
                logout: 'logout'
            }),
            clickLogout() {
                this.$router.push({name: 'home'})

                this.logout()
            }
        }

    }
</script>

<style scoped>

    .info-container {
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        padding: 1.5rem 0;
        width: 90%;
        display: flex;
        align-items: center;
        margin-left: 5%;
        border: .1rem none #33333333;
        border-bottom-style: solid;
    }

    .icon {
        width: 6rem;
        height: 6rem;
        border: .2rem solid lightgrey;
        border-radius: 4rem;
    }

    .info-content {
        display: flex;
        flex-direction: column;
        margin-left: 1rem;
    }

    .info-title {
        font-size: 1.8rem;
    }

    .info-sub {
        font-size: 1.2rem;
    }

    .list-item {
        width: 90%;
        height: 5rem;
        display: flex;
        align-items: center;
        margin-left: 5%;
        border: .1rem none #33333333;
        border-bottom-style: solid;
        font-size: 1.2rem;
        justify-content: space-between;
    }

    .list-item > span, .lucky-item > span {
        margin: 0 .8rem;
    }

    .list-item:active {
        background-color: #33333333;
        transition: all .4s;
    }

    .lucky-item {
        margin-top: 1rem;
        width: 90%;
        display: flex;
        flex-direction: column;
        margin-left: 5%;
        border: .1rem none #33333333;
        border-bottom-style: solid;
        font-size: 1.2rem;
    }

    .ball-container {
        width: 100%;
        margin-top: .8rem;
        display: flex;
        justify-content: center;
    }

    .btn-buy-container {
        width: 100%;
        display: flex;
        justify-content: flex-end;
    }

    .btn-buy {
        margin: 1rem;
        height: 2.5rem;
        border: .1rem solid #33333333;
        border-radius: .3rem;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0 1rem;
    }

    .btn-buy:active {
        background-color: #33333333;
        transition: all .3s;
    }

    .ball {
        width: 2.5rem;
        height: 2.5rem;
        display: flex;
        align-items: center;
        justify-content: center;
        border: .12rem solid rgba(229, 28, 35, 1);
        border-radius: 1.5rem;
        margin-right: .8rem;
        color: rgba(229, 28, 35, 1);
    }

    .ball.blue {
        color: rgba(0, 0, 255, 1);
        border: .12rem solid rgba(0, 0, 255, 1);
    }

    .btn-logout {
        width: 90%;
        height: 4rem;
        margin-left: 5%;
        margin-top: 1.5rem;
        margin-bottom: 1.5rem;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 1.5rem;
        border-radius: .3rem;
        background-color: red;
        color: white;
    }

    .btn-logout:active {
        background-color: #33333333;
        transition: all .4s;
    }

</style>